
import Vue from 'vue'
import App from './views/app.vue'
import Web3 from 'web3'

// Static files

require('./public/css/bootstrap.min.css');
require('./public/css/style.css');

const routes = {
    '/': 'Main'
}

window.addEventListener('load', () => {

    const test_url = 'http://localhost:8545';
    const provider_url = 'https://mainnet.infura.io/DU9hw1K6no7QQUcifyRc';

    // Create web3 object after page load
    if (typeof web3 !== 'undefined') {
        window.web3 = new Web3(window.web3.currentProvider);
    } else {
        window.web3 = new Web3(new Web3.providers.HttpProvider(provider_url));
        console.log(`Connecting to Ethereum node on RPC @ ${provider_url}`);
    }

    new Vue({
        el: '#app',
        render: h => h(App),
        data: {
            currentRoute: window.location.pathname,
            view: '',
            detail: ''
        },
        watch: {
            currentRoute: function (val, oldVal) {

                let path = this.currentRoute.split('/');
                this.view = routes['/'+ path[1]] || 'notfound';
                this.detail = path[2] || '';
            }
        },
        created: function () {

            // Set route
            let path = this.currentRoute.split('/');
            this.view = routes['/'+ path[1]] || 'notfound';
            this.detail = path[2] || '';

            // Listen to popstate
            window.addEventListener('popstate', () => this.updateRoute());
        },
        methods: {
            updateRoute()
            {
                this.currentRoute = window.location.pathname;
            }
        }
    })
});



