
<template>
    <div id="app">
        <navbar :apptitle = "apptitle" :conn = "connected"></navbar>
        <div id="main-container" class="container-fluid">
            <div class="col-sm-2">
                <h1>Hello</h1>
                <home></home>
            </div>
            <div class="col-sm-10" id="main">
                <h2><i class="glyphicon glyphicon-book" style="font-size: 0.7em"></i>
                    &nbsp; Latest blocks
                </h2>
                <tableview></tableview>
            </div>

        </div>
    </div>
</template>

<script>

import navbar from './navbar.vue';
import home from './home.vue';
import tableview from './tableview.vue';

export default {
    components: {
        home,
        navbar,
        tableview
    },
    data: function() {
        return {
            apptitle: 'Eth-Link Explorer',
            connected: false
        }
    },
    created: function () {

        window.web3.eth.getBlockNumber().then(function(b) {
            console.log('We\'re on block number '+ b);
            this.connected = true;
        }.bind(this));
    }
}
</script>


