
<template>
    <table class="table table-dark big">
        <thead>
            <tr>
                <th>Block #</th>
                <th>Tx Length</th>
                <th>Size</th>
                <th>Timestamp</th>
            </tr>
        </thead>
        <tbody class="table-hover">
            <blockItem v-for = "block in itemList" 
                :key = "block.id" :block = "block"></blockItem>
        </tbody>
    </table>
</template>

<script>

import blockItem from './components/block-item.vue';

export default {
    data() {
        return {
            itemList: [],
        }
    },
    components: {
        blockItem
    },
    methods: {
        processBlock: function(b)
        {
            if (b === null)
                return;

            console.log('processing block '+ b.number);

            this.itemList.push({
                id: b.number,
                size: b.size,
                txLength: b.transactions.length,
                time: this.convertTimestamp(b.timestamp)
            });
        },
        convertTimestamp: function (timestamp) {

            let date = new Date(timestamp * 1000);      
            return date.toLocaleDateString() +' '+ date.toLocaleTimeString();
        }
    },
    mounted: function() {
        
        // List latest blocks
        const maxBlocks = 25;
        let promises = [];

        window.web3.eth.getBlockNumber().then(function(b) {

            for (var i = 0; i < maxBlocks; i++) {
                promises.push(window.web3.eth.getBlock(b - i).then(this.processBlock));
            }

            Promise.all(promises).then(() => { 
                this.itemList.sort((a, b) => b.id - a.id);
                console.log('sorted');
            });

        }.bind(this));
    }
}

</script>


