
<template>
    <tr>
        <td><vlink v-bind:href="blockID">{{ block.id }}</vlink> &nbsp;</td>
        <td>{{ block.txLength }} &nbsp;</td>
        <td>{{ block.size }} &nbsp;</td>
        <td>{{ block.time }} &nbsp;</td>
    </tr>
</template>

<script>

import vlink from "./v-link.vue"

export default {
    components: {
        vlink
    },
    props: {
        block: {
            type: Object,
            required: true 
        }
    },
    computed: {
        blockID () {
            return '/block/'+ this.block.id;
        }
    }
}
</script>


