
<template>
    <nav class="navbar navbar-default">
        <div class="container-fluid">
            <div class="col-sm-2">
                <vlink v-cloak class="navbar-brand" href="/">
                    <i class="glyphicon glyphicon-random"></i>&nbsp; {{ apptitle }} </vlink>
            </div>

            <!-- Search box -->
            <div class="col-sm-4 form-group inner-addon right-addon">
                <form @submit.prevent="sendRequest" name="ethRequest">
                    <i class="glyphicon glyphicon-search"></i>
                    <input type="text" class="form-control" v-model="ethRequest" id="master-input" name="master-input"
                        @keyup="textchange" @keydown="textchange" placeholder="Enter Tx Hash / Address / Block"/>
                </form>
            </div>

            <div v-cloak class="col-sm-6 pull-right" style="padding-top: 7px">
                <span class="pull-right">&nbsp; {{ conn ? "connected" : "disconnected" }} </span>
                <i v-if="conn" class="glyphicon glyphicon-flash pull-right" style="padding-top: 4px"></i>
                <i v-if="conn === false" class="glyphicon glyphicon-remove-circle pull-right" style="padding-top: 4px"></i>
            </div>
        </div>
    </nav>
</template>

<script>

import vlink from "./components/v-link.vue";

export default {
    components: {
        vlink
    },
    props: {
        apptitle: {
            type: String,
            required: true
        },
        conn: {
            type: Boolean,
            required: true 
        }
    },
    data: function() {
        return {
            ethRequest: ''
        }
    },
    methods: {
        textchange: function (event) {

            let el = document.getElementById(event.target.id);
            el.style['font-size'] = (el.value.length < 52) ? '1.25em' : '0.92em';
        },
        goToLocation: function (route, request)
        {
            let path = '/'+ route +'/'+ request;
            this.$root.currentRoute = path;
            window.history.pushState(null, routes['/'+ route], path);
        },
        sendRequest: function (event) {

            let request = this.ethRequest.split('0x').join('');

            if(/[0-9a-fA-F]{40,64}/.test(request)) {
                if (request.length === 40) 
                    this.goToLocation('addr', '0x'+ request);

                if (request.length === 64)
                    this.goToLocation('tx', '0x'+ request);
            }
            else if(/[0-9]{1,8}/.test(request)) {
                this.goToLocation('block', parseInt(request));
            }
        }
    }
}
</script>


